@import 'vars';
@import 'mixins';

.articles {
    background-color: #ffffff;
}
.article {
    // max-width: 950px;
    padding: 35px 250px 30px 40px;
    // background-color: #ffffff;
    border-bottom: 1px solid #f3f3f3;
    @include clearfix;
    // &:first-child {
    //     padding-top: 20px;
    // }
    &__pic {
        width: 160px;
        height: 160px;
        float: left;
        img {
            max-width: 100%;
            display: block;
            height: auto;
            object-fit: cover;
        }
    }
    &__content {
        float: left;
        margin-left: 30px;
        width: 79%;
    }
    &__title {
        color:  map_get($gray, default);
        font-family: $merri-font;
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 10px;
    }
    &__date {
        color: #a6a6a6;
        font-size: 12px;
        font-weight: 400;
        margin-bottom: 15px;
    }
    &__text {
        color:  map_get($gray, default);
        font-size: 14px;
        font-weight: 400;
        margin-bottom: 15px;
        line-height: 20px;
        text-align: justify;
    }
}
.read-more-link {
    display: inline-block;
    color: map_get($blue, default);
    font-size: 14px;
    font-weight: 500;
    text-decoration: underline;
    transition: .3s;
    &:hover {
        color: map_get($blue, hover);
        text-decoration: underline;
    }
    &:active {
        color: map_get($blue, active);
        text-decoration: underline;
    }
}


.open-article {
    background-color: #ffffff;
    padding: 35px 30px 30px 40px;
    max-width: 840px;
    &__title {
        color:  map_get($gray, default);
        font-family: $merri-font;
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 10px;
    }
    &__date {
        color: #a6a6a6;
        font-size: 12px;
        font-weight: 400;
        margin-bottom: 35px;
    }
    &__text {
        color:  map_get($gray, default);
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 35px;
        text-align: justify;
    }
    &__pic {
        width: 490px;
        // height: 490px;
        margin-bottom: 30px;
        img {
            display: block;
            max-width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
}

.float-block-wrap {
    position: relative;
    background: #ffffff;
}

.float-block {
    position: absolute;
    top: 15px;
    right: 30px;
    box-shadow: 0 1px 5px 1px rgba(29, 25, 41, 0.22);
    background-color: #ffffff;
    padding-top: 25px;
    padding-bottom: 35px;
    width: 330px;
    &__title {
        color: #000000;
        font-size: 16px;
        font-weight: 500;
        margin-bottom: 25px;
        padding-left: 30px;
    }
}

.last-news {
    @include clearfix;
    border-bottom: 1px solid #f3f3f3;
    padding: 20px 30px;
    &:first-child {
        padding-top: 0;
    }
    &:last-child {
        border-bottom: none;
        padding-bottom: 0;
    }
    &__pic {
        float: left;
        display: block;
        width: 74px;
        height: 74px;
        img {
            display: block;
            max-width: 100%;
            height: auto;
            object-fit: cover;
        }
    }
    &__content {
        float: left;
        margin-left: 20px;
    }
    &__title {
        display: block;
        color: map_get($gray, default);
        font-family: $merri-font;
        font-size: 14px;
        font-weight: 700;
        margin-bottom: 10px;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, hover);
        }
    }
    &__date {
        color: map_get($gray-light, default);
        font-size: 12px;
        font-weight: 400;
    }

}

.fixed-float-block {
    position: fixed;
    right: auto;
    margin-top: 0;
    top: 100px !important;
}

.fixed-bottom-float-block {
    position: absolute;
    top: auto !important;
    bottom: 20px;
}